@use 'base/mixins' as m;
@use 'base/variables' as v;

// Paleta
$bg: #0b0b0b;
$fg: #ffffff;
$muted: #bdbdbd; // textos secundarios
$accent: #990617; // rojo del título y punto

// Helpers
@mixin container {
  max-width: 1200px;
  margin-inline: auto;
  padding-inline: clamp(16px, 2vw, 32px);
}

.noticias {
  padding-block: clamp(32px, 6vw, 80px);

  &__container { @include container; }

  &__title {
    text-align: center;
    font-weight: 800;
    letter-spacing: .2px;
    line-height: 1.1;
    font-size: clamp(28px, 4.5vw, 44px);
    margin: 1rem;

    &--accent { color: $accent; }
  }

  &__dot {
    color: $accent;
    margin-left: 2px;
  }

  &__subtitle {
    text-align: center;
    margin: 0 auto clamp(24px, 5vw, 48px);
    max-width: 850px;
    color: $fg;
    font-size: clamp(14px, 1.8vw, 16px);
  }

  &__grid {
    display: grid;
    gap: clamp(16px, 3vw, 28px);
    grid-template-columns: 1fr;

    @media (min-width: 640px) {
      grid-template-columns: repeat(2, 1fr);
    }
    @media (min-width: 1024px) {
      grid-template-columns: repeat(4, 1fr);
    }
  }

  &__brand {
    text-align: center;
    margin-top: clamp(28px, 6vw, 56px);
    color: $fg;

    strong { color: $fg; }
  }

  &__contenedor--enlace {
    display: flex;
    justify-content: flex-end
  }

  &__contenedor--enlace a{
    color: #e4041e;
    font-size: 1.5rem;
    font-weight: v.$bold;
  }
  &__contenedor--enlace a:hover{
    cursor:pointer;
    color: v.$blanco;
  }
}

// Card
.noticias-card {
    cursor: pointer;
  display: flex;
  flex-direction: column;
  gap: 10px;
  border-radius: 14px;
  padding: 10px; // mantiene el look “flotante” sobre fondo negro
  transition: transform .25s ease, box-shadow .25s ease;
  will-change: transform;

  &:hover {
    transform: translateY(-4px);
    box-shadow: 0 12px 30px rgba(0,0,0,.35);
  }

  &__media {
    aspect-ratio: 16 / 9;
    border-radius: 14px;
    overflow: hidden;
    margin: 0;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      display: block;
      transform: scale(1.0);
      transition: transform .35s ease;
    }
  }

  &:hover &__media img { transform: scale(1.05); }

  &__date {
    font-size: 12px;
    color: $fg;
  }

  &__title {
    font-size: clamp(16px, 2vw, 18px);
    font-weight: 800;
    line-height: 1.25;
    margin: 0;
  }
}

/* Modal base */
.modal {
  position: fixed;
  inset: 0;
  z-index: 9999;
  display: none;
  &.is-open { display: block; }

  .modal__overlay {
    position: absolute;
    inset: 0;
    background: rgba(0,0,0,.55);
    backdrop-filter: blur(2px);
    z-index: 0;
  }

  .modal__dialog {
    position: relative;
    z-index: 1;
    max-width: min(900px, 92vw);
    max-height: min(90vh, 900px);
    margin: 5vh auto;
    background: #111; /* o tu color base */
    color: #fff;
    border-radius: 16px;
    padding: 24px;
    overflow: auto;
    box-shadow: 0 10px 30px rgba(0,0,0,.4);
    animation: modalIn .18s ease-out;
  }

  .modal__close {
    position: sticky;
    top: 0;
    margin-left: auto;
    border: 0;
    background: transparent;
    font-size: 28px;
    line-height: 1;
    cursor: pointer;
    color: #bbb;
    &:hover { color: #fff; }
  }

  .modal__title {
    font-size: clamp(1.25rem, 2.5vw, 1.8rem);
    margin: 8px 0 4px;
  }

  .modal__date {
    display: block;
    font-size: .95rem;
    color: #bbb;
    margin-bottom: 16px;
  }

  .modal__body {
    line-height: 1.6;
    font-size: 1rem;
    white-space: pre-wrap;
  }
}

@keyframes modalIn {
  from { transform: translateY(8px); opacity: .8; }
  to   { transform: translateY(0);   opacity: 1;  }
}

/* Bloquear scroll del fondo cuando modal está abierto */
body.no-scroll { overflow: hidden; }



.noticia {
  display: block;

  &__titulo {
    text-align: center;
    margin: 5rem;
  }

  &__contenedor_imagen {
    display: flex;
    width: 60rem;

    img {
      border-radius: 2rem;
      width: 100%;
      height: auto;
    }
  }

  &__contenido {
    display: flex;
    align-items: center;
    align-content: center;

    &--texto {
      margin: 2rem;
      padding: 3rem;
    }
  }

  &__creditos {
    display: flex;             // mejor que grid para separar en extremos
    justify-content: space-between;
    align-items: center;
    font-size: 1.3rem;
    margin-top: 2rem;

    div p span {
      color: #e4041e;
      font-weight: v.$bold;
    }
  }

  // Media query para tablet en adelante
  @include m.tablet {
    background-color: #00000077;
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 1rem;
    margin: 4rem;
    align-items: center;
    justify-items: center;
  }
}






