@use 'variables' as v;
@use "sass:color";

/** Media Queries **/
@mixin telefono {
  @media (min-width: v.$telefono){
    @content;
  }
}

@mixin tablet {
  @media (min-width: v.$tablet){
    @content;
  }
}

@mixin desktop {
  @media (min-width: v.$desktop){
    @content;
  }
}

@mixin xl_desktop {
  @media (min-width: v.$xl-sdesktop){
    @content;
  }
}

@mixin grid($columnas, $espaciado) {
  display: grid;
  gap: $espaciado;
  grid-template-columns: repeat($columnas, 1fr);
}

@mixin contenedor {
  width: min(95%, 140rem);
  margin: 0 auto;
}

@mixin contenedor-md {
  width: min(95%, 80rem);
  margin: 0 auto;
}

@mixin contenedor-sm {
  width: min(95%, 60rem);
  margin: 0 auto;
}

@mixin headings {
  font-family: v.$fuente_principal;
  font-weight: v.$black;
  margin: 2rem 0;
}

@mixin logo{
  color: v.$blanco;
  text-align: center;
  margin-bottom: 2rem;
  @include desktop{
    text-align: left;
    margin: 0;
  }
}

@mixin boton($bgColor, $colorTexto) {
    background-color: $bgColor;
    padding: 1.4rem 3rem;
    color: $colorTexto;
    font-size: 1.8rem;
    font-weight: 700;
    border: none;
    transition-property: background-color;
    transition-duration: .3s;
    text-align: center;
    width: 100%;
    display: inline-block;

    &:hover {
        background-color: color.adjust($bgColor, $lightness: 7%);
        cursor: pointer;
    }
}

@mixin texto-fluido($min, $max){
  &{
    font-size: clamp($min, -0.8rem + 10vw, $max);
  } 
}

@mixin enlace{
    all: unset;
    color: v.$blanco;
    font-size: 1.4rem;
    text-transform: uppercase;
    font-weight: v.$bold;

    &:hover {
        color: v.$gris;
        cursor: pointer;
    }
}

@mixin shadow {
  box-shadow: rgba(50, 50, 93, 0.25) 0px 13px 27px -5px, rgba(0, 0, 0, 0.3) 0px 8px 16px -8px;
}

@mixin reset-lista {
    margin: 0;
    padding: 0;
    list-style: none;
}

@mixin tags {
    @include reset-lista;
    display: flex;
    flex-wrap: wrap;
    gap: 1.5rem;
    margin-top: 1.5rem;

    > li {
        background-color: v.$azul;
        padding: 1rem;
        border-radius: .5rem;
        font-size: 1.4rem;
        font-weight: v.$bold;
        color: v.$blanco;
    }
}


@mixin bg-gradient($bg_color) {
    background-image: linear-gradient(to left top, $bg_color, color.adjust($bg_color, $lightness: 1%));
}