@use 'base/mixins' as m;
@use 'base/variables' as v;
@use 'sass:color';

.dashboard {
    background-color: v.$gris-claro;
    height: 100%;
    display: flex;
    flex-direction: column;
    &__header {
        background-color: v.$mint;
        padding: 2rem;
    }
    &__header-grid{
        display: flex;
        flex-direction: column;
        @include m.tablet {
            flex-direction: row;
            justify-content: space-between;
            align-items: center;
        }
    }
    &__heading {
        color: v.$negro;
        text-align: center;
    }

    &__logo {
        @include m.logo;
        @include m.texto-fluido(2rem, 3rem)
    }

    &__form{
        display: flex;
        justify-content: center;
    }

    &__submit--logout {
        @include m.enlace;
    }

    &__grid {
        display: flex;
        flex: 1;
        overflow-y: auto;
    }

    &__sidebar {
        background-color: v.$mint;
        overflow-y: scroll;
        overflow-x: hidden;
        
        width: 12rem;
    }

    &__menu {
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 2rem;
        transition: 300ms background-color ease-in-out, 300ms color ease, 300ms border-radius ease;
    }
    &__enlace {
        color: color.adjust(v.$mint, $lightness: -30%);
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 2rem;
        transition: 300ms background-color ease-in-out, 300ms color ease, 300ms border-radius ease;
        &:hover,
        &--actual {
            color: color.adjust(v.$gris-oscuro, $lightness: 20%);
            color: v.$blanco;
            border-radius: 1rem;
        }
    }
    &__icono {
        font-size: 2rem;
    }

    &__menu-texto {
        font-size: 1.1rem;
        text-transform: uppercase;
        font-weight: v.$bold;
        margin-top: .5rem;
    }

    &__contenido{
        width: 100%;
        padding: 1rem 1rem 3rem 1rem;
        overflow-y: scroll;
        @include m.tablet {
            padding: 1rem 5rem 3rem 5rem;
        }
    }

    &__contenedor-boton {
        @include m.tablet {
            display: flex;
            justify-content: flex-end;
            margin-bottom: 5rem;
        }
    }

    &__boton {
        @include m.boton(v.$lapiz_lazuli, v.$blanco);
        text-transform: uppercase;
        font-size: 1.4rem;
        @include m.tablet {
            width: auto;
        }
    }

    &__contenedor{
        @include m.shadow;
    }

    &__formulario {
        @extend .dashboard__contenedor;
        @include m.contenedor-md;
        padding: 1rem;
    }

    &__garfica {
        @include m.contenedor-sm;
    }

    &__void{
        color: v.$negro;
        padding: 1rem;
    }




}